@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto Condensed', sans-serif;
}

button {
  outline: none;
  border: none;
  cursor: pointer;
  color: black;
  padding: 7px 10px;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
  transition: all .3s ease-in-out;
}

.userInput {
  font-size: 20px;
  flex-direction: row;
}

.userInput input {
  font-size: 18px;
  padding: 6px 15px;
  border: none;
  outline: none;
  font-family: 'Roboto Condensed', sans-serif;
}

.add-btn {
  background-color: rgb(0, 251, 255);
}

.add-btn:hover {
  background-color: rgb(33, 224, 45);
}

.heading {
  color: rgb(255, 255, 255);
  padding: 2rem 0;
  font-size: 2.5rem;
  letter-spacing: 2px;
}

.dashboard {
  background-color:rgb(36, 13, 95);
  height: auto;
  width: 400px;
  margin: 2rem auto;
  flex-direction: column;
}

.flex {
  display: flex;
  align-items: center; 
}

.tasks {
  margin: 3rem 0 4rem 0;
}

.task {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin: 1rem 0;
  background-color: white;
  padding: .5rem 1rem;
  justify-items: center;
  align-items: center;
  width: 320px;
}

.icon {
  font-size: 35px;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.complete-btn {
  color: rgb(36, 13, 95);
}

.complete-btn:hover {
  color: rgb(5, 182, 17);
}

.delete-btn {
  color: rgb(36, 13, 95);
}

.delete-btn:hover {
  color: rgb(240, 44, 54);
}

.counter {
  color: white;
}

.clear-btn{
  background: rgb(255, 255, 255);
  margin: 2rem 0;
}

.clear-btn:hover{
  background: rgb(0, 251, 255);
}